                                    </ion-label>
                                    <ion-label slot="end">
                                        <ion-skeleton-text :animated="true" style="width: 60%; height: 20px;" />
                                    </ion-label>
                                </ion-item>
                            </div>
                            <ion-list v-else>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">NPID</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.patient?.national_patient_id }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">ART Regimen</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.order?.art_regimen }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">ARV Number</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.order?.arv_number }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Patient Name</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ patientName }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Age</ion-label>
