                            </div>
                            <ion-list v-else>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Type</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.order?.sample_type?.name }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Tracking Number</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.order?.tracking_number }}</ion-label>
                                </ion-item>
                                <ion-item>
                                    <ion-label style="font-size: 20px; font-weight: 600">Status</ion-label>
                                    <ion-label slot="end" style="font-size: 18px;">{{ details?.order?.sample_status?.name }}</ion-label>
