                            <ion-toolbar color="primary">
                                <ion-title> Results </ion-title>
                            </ion-toolbar>
                            <div v-if="loading">
                                <ion-item v-for="n in 4" :key="n">
                                    <ion-label>
                                        <ion-skeleton-text :animated="true" style="width: 40%; height: 20px;" />
                                    </ion-label>
                                    <ion-label slot="end">
                                        <ion-skeleton-text :animated="true" style="width: 60%; height: 20px;" />
                                    </ion-label>
                                </ion-item>
                            </div>
                            <div v-else>
                                <ion-grid>
                                    <template v-for="(test, testIndex) in details?.tests" :key="testIndex">
                                        <ion-row v-if="test.test_results?.length > 0">
