                </ion-button>

                <ion-button @click="mergeOrder" size="large" slot="end" color="success">
                    <ion-icon :icon="add" slot="icon-only" style="margin-right: 5px;"></ion-icon>
                    Merge
                </ion-button>
                <ion-button @click="printTrackingNumber" size="large" slot="end">
