<script setup lang="ts">
import {
    IonPage, IonIcon, IonContent, IonToolbar, IonHeader, IonTitle, IonFooter, IonButton,
    IonCol, IonRow, IonGrid, IonCard, IonSkeletonText, IonItem, IonLabel, IonList, IonItemDivider, IonChip
} from "@ionic/vue";
import { add, print, arrowBack } from "ionicons/icons";
import { Client } from '@/composables/client';
import { computed, onMounted, ref } from 'vue';
import { useRoute } from 'vue-router';
import moment from 'moment';
import { DATE_FORMAT } from '@/utils/Formats';
import { toastDanger, toastSuccess } from "@/utils/Alerts";
import router from "@/router";
import PrintService from "@/services/print_service";
import { LabLocation } from "types";

interface PatientInfo {
    id: string;
    first_name: string;
    last_name: string;
    gender: string;
    date_of_birth: string;
    national_patient_id?: string;
}

interface TestResult {
    measure: {
        name: string;
        preferred_name: string;
    };
    result: {
        value: string;
        unit: string;
    };
}

interface StatusTrailEntry {
    status_id: number;
    status: string;
    timestamp: string;
    updated_by: {
        first_name: string;
        last_name: string;
        id: string;
        phone_number: string;
    };
}

interface TestType {
    id: number;
    test_category_id: number;
    name: string;
    short_name: string;
    targetTAT: string;
    description: string;
    preferred_name: string;
    scientific_name: string;
    can_be_done_on_sex: string;
    assay_format: string;
    hr_cadre_required: string;
    iblis_mapping_name: string;
}

interface Test {
    uuid: string;
    tracking_number: string;
    arv_number: string;
    test_status: string;
    time_updated: string;
    test_type: TestType;
    status_trail: StatusTrailEntry[];
    test_results: TestResult[];
}

interface NLIMSData {
    order: {
        tracking_number: string;
        sample_type: {
            name: string;
            preferred_name: string;
        };
        sample_status: {
            name: string;
        };
        date_created: string;
        priority: string;
        requested_by: string;
        drawn_by: {
            name: string;
        };
        sending_facility: string;
        target_lab: string;
        order_location: string;
        arv_number: string;
        art_regimen: string;
    };
    patient: PatientInfo;
    tests: Test[];
}

const route = useRoute();
const details = ref<NLIMSData | null>(null);
const loading = ref<boolean>(false);

const patientName = computed((): string => {
    const patient = details.value?.patient;
    if (patient) {
        return `${patient.first_name} ${patient.last_name}`;
    }
    return "";
});

const patientAge = computed((): string => {
    const patient = details.value?.patient;
    if (patient) {
        const now = moment();
        const birthDate = moment(patient.date_of_birth);
        const years = now.diff(birthDate, 'years');
        birthDate.add(years, 'years');
        const months = now.diff(birthDate, 'months');
        return `${years} years ${months} months`;
    }
    return "";
});


const dateOrdered = computed((): string => {
    const createdDate = details.value?.order?.date_created;
    if (createdDate) {
        return moment(createdDate).format(DATE_FORMAT);
    }
    return "";
});

const searchNLIMS = async (trackingNumber: string): Promise<void> => {
    const client = Client();
    loading.value = true;
    try {
        const res = await client.getJson('orders/search_order_from_nlims_by_tracking_number', {
            tracking_number: trackingNumber.replace(/\$$/, '')
        });
        if (res?.ok) {
            console.log('NLIMS API Response:', res.data); // Debug log
            details.value = res.data;
        } else {
            toastDanger(res?.error == "Request not found" ? "Order not available in NLIMS" : res?.error);
            setTimeout(() => {
                router.back();
            }, 1000)
        }
    } catch (e: any) {
        console.error("error occurred while searching for order in nlims: ", e);
        toastDanger("error occurred while searching for order in nlims")
    } finally {
        loading.value = false;
    }
};

const mergeOrder = async (): Promise<void> => {
    const client = Client();
    const { getLocation, token } = client;

    const userData = token.value?.authorization?.user;
    const labLocation = userData?.lab_locations.find(
        (location: LabLocation) => location.name === getLocation()
    );

    try {
        const res = await client.postJson('orders/merge_order_from_nlims', {
            ...details.value,
            lab_location: labLocation?.id
        });
        if (res?.ok) {
            details.value = res.data;
            toastSuccess("Order merged successfully");
            await router.push('/tests');
        } else {
            toastDanger(res?.error || "An error occurred while merging order");
        }
    } catch (error) {
        console.error('Error merging order:', error);
        toastDanger("An error occurred while merging order");
    }
}

const printTrackingNumber = () => {
    const printService = new PrintService();
    const tracking_number = String(details.value?.order?.tracking_number);
    printService.writeLbl(
        "printout/tracking_number",
        { tracking_number },
        `${tracking_number}.lbl`
    );
}

onMounted(() => {
    const trackingNumber = route.query['tracking-number'];
    if (typeof trackingNumber === 'string') {
        searchNLIMS(trackingNumber);
    }
});

const hasResults = computed(() => {
    return details.value?.tests && details.value.tests.length > 0;
});

const getStatusColor = (status: string | undefined): string => {
    switch (status) {
        case 'Completed':
            return 'success';
        case 'Pending':
            return 'warning';
        case 'Cancelled':
            return 'danger';
        default:
            return 'medium';
    }
};
</script>
